@import "../../frontend-shell/node_modules/include-media/dist/include-media";
@import "../../frontend-shell/shared/main";

.org-page {
  svg {
    max-width: 100%;
  }

  .dropdown__item {
    button {
      display: block;
      white-space: nowrap;
      font-size: 18px;
      font-weight: bold;
      color: #2c3756;
      text-decoration: none;
      padding: 0;
      border: 0;
      background: transparent;
      font-family: $default-font;
      cursor: pointer;
      outline: none;

      &.danger {
        color: $danger-red-color;

        &:hover {
          color: $danger-red-color-active;
        }
      }
    }
  }
}

.org-list {
  margin-bottom: 80px;
  max-width: 840px;

  &:last-child {
    margin-bottom: 0;
  }
}

